        2: "Überall gibt es Kriminalität, in manchen Ländern mehr, in manchen weniger. Es werden Menschen beklaut, verletzt oder sogar getötet. Sie werden ohne Verurteilung durch ein Gericht widerrechtlich festgehalten oder sie müssen aus ihrer Heimat fliehen. Gründe für diese Ungerechtigkeiten sind zum einen die Gesellschaft, die leider noch sehr weit entfernt davon ist, friedlich zu sein (das sieht man alleine schon daran, wie oft sich Menschen streiten), als auch eine Justiz (also die Rechtsprechung, die in Gerichten vollzogen wird), die teilweise versagt und eine Exekutive (also z.B. die Polizei), die Verbrechen nicht oft genug verhindern und / oder nicht immer nachverfolgen kann. Es gibt nach wie vor viele ungelöste Verbrechen, deren Täter bis heute frei umherlaufen können.",
